import '../assets/styles/styles.scss';

// fonctions
const removeClass = (elements, classRemoved) => {
    for (const element of elements) {

        element.classList.remove(classRemoved);
    }
}

const createHeaderItemBarHTML = (...elements) => {
    let html = '';
    for (const element of elements) {
        const location = window.location.href.split('/');
        const currentPage = location[location.length - 1];
        console.log(currentPage, element.includes(currentPage));
        // verifier si l'item correspond à la page actuelle
        if (currentPage.includes('.html') && element.includes(currentPage)) {
            html += `<span style="background-color:var(--blue-primary)">${element}</span>`;
        } else {
            html += `<span>${element}</span>`;
        }
    }
    const result = '<nav>' + html + '</nav>';
    return result;
};


// construction de la top-bar
class TopBar extends HTMLElement {
    connectedCallback() {
        this.innerHTML = `
            <header class="down-scroll">
                <div class="container-1">
                    <div class="logo-container">
                        <img id="header-logo" src="assets/images/cropped-image_2023-08-10_142556159-removebg-preview.png"
                            alt="logo de l'entreprise ParlonsPC">
                    </div>
                    <div class="menu-container">
                        
                        <p id="services">NOS SERVICES</p>
                        <p id="materiel">ACHETER DU MATERIEL</p>
                        <p id="entreprise">L'ENTREPRISE</p>
                        <p id="question">EN SAVOIR PLUS</p>
                        
                    </div>
                    <div class="nous-rejoindre">
                        <span class="point"></span>
                        <p>CONTACTEZ-NOUS<br>contact@parlonspc.fr</p>
                    </div>
                </div>
                <div class="container-2" id="container-2">
                </div>
            </header>
      `;
    }
}

// balyse pour la top-bar
customElements.define('top-bar', TopBar);


// top-bar - redirection du logo
const headerlogoElement = document.querySelector('#header-logo');
headerlogoElement.addEventListener('click', event => {
    location = './index.html';
})

// une fois la page chargée
window.addEventListener('DOMContentLoaded', event => {

    // detection du scroll vers le bas pour afficher la top-bar
    const headerElement = document.querySelector('header');
    const DScrollElement = document.querySelector('.down-scroll')
    const contentElement = document.querySelector('.content');
    let lastScrollY;

    document.addEventListener('scroll', event => {

        if (scrollY > 0) {
            headerElement.classList.add('down-scroll');
            contentElement.style = 'margin-top: 170px';
        } else {
            headerElement.classList.remove('down-scroll');
            contentElement.style = 'margin-top: 50px';
        }

        if (scrollY < lastScrollY) {
            DScrollElement.style.top = '0px';
        } else if (scrollY > lastScrollY) {
            DScrollElement.style.top = '-120px';
        }
        lastScrollY = scrollY;
    })

    // construction de la barre d'items
    const servicesElement = document.getElementById('services');
    const materielElement = document.getElementById('materiel');
    const entrepriseElement = document.getElementById('entreprise');
    const questionElement = document.getElementById('question');

    const itemsBarElement = document.getElementById('container-2');

    // active par default:
    servicesElement.classList.add('active');
    itemsBarElement.innerHTML = createHeaderItemBarHTML(
        '<a id="audit">Audit</a>',
        '<a id ="cybersecurite" href="cybersecurite.html">Cybersécurité</a>',
        '<a id="infogerance" href="infogerance.html">Infogérance</a>',
        '<a id="cloud-computing" href="cloud_computing.html">Cloud Computing</a>',
        '<a>Maintenance informatique</a>',
        '<a>Site internet</a>',
        '<a>Téléphonie et Internet</a>',
        '<a>Support informatique</a>',
    );

    // services
    servicesElement.addEventListener('click', event => {
        if (servicesElement.classList.contains('active')) {
            itemsBarElement.innerHTML = null;
            removeClass([event.currentTarget], 'active');
        } else {
            removeClass([materielElement, entrepriseElement, questionElement], 'active');
            servicesElement.className = 'active';

            itemsBarElement.innerHTML = createHeaderItemBarHTML(
                '<a>Audit</a>',
                '<a href="cybersecurite.html">Cybersécurité</a>',
                '<a href="infogerance.html">Infogérance</a>',
                '<a id="cloud-computing" href="cloud_computing.html">Cloud Computing</a>',
                '<a>Maintenance informatique</a>',
                '<a>Site internet</a>',
                '<a>Téléphonie et Internet</a>',
                '<a>Support informatique</a>',
            );
        }
    })

    // acheter du materiel
    materielElement.addEventListener('click', event => {
        if (materielElement.classList.contains('active')) {
            itemsBarElement.innerHTML = null;
            removeClass([event.currentTarget], 'active');
        } else {
            removeClass([servicesElement, entrepriseElement, questionElement], 'active');
            materielElement.classList.add('active');
            itemsBarElement.innerHTML = createHeaderItemBarHTML(
                '<a>Mac OS</a>',
                '<a>Windows</a>',
                '<a>Configuration sur Mesure</a>',
                '<a>Reconditionné</a>',
                '<a>Service après-vente et livraison</a>',
            );
        }
    })
    // l'entreprise
    entrepriseElement.addEventListener('click', event => {
        if (entrepriseElement.classList.contains('active')) {
            itemsBarElement.innerHTML = null;
            removeClass([event.currentTarget], 'active');
        } else {
            removeClass([servicesElement, materielElement, questionElement], 'active');
            entrepriseElement.classList.add('active');
            itemsBarElement.innerHTML = createHeaderItemBarHTML(
                '<p>Nos Valeurs</p>',
                '<p>Fonctionnement</p>',
                '<p>Témoignages de nos clients</p>',
                '<p>Victor, le dirigeant</p>',
                '<p>Notre brochure</p>',
            );
        }
    })
    // en savoir plus
    questionElement.addEventListener('click', event => {
        if (questionElement.classList.contains('active')) {
            itemsBarElement.innerHTML = null;
            removeClass([event.currentTarget], 'active');
        } else {
            // enleve la class 'active' aux autres elements
            removeClass([servicesElement, materielElement, entrepriseElement], 'active');

            questionElement.classList.add('active');
            itemsBarElement.innerHTML = createHeaderItemBarHTML(
                '<a>Nous poser une question</a>',
                '<a>Prendre un Rendez-Vous</a>',
                '<p>Nous suivre sur les réseaux</p>',
                '<p>Coordonnées</p>',
            );
        }
    })
})

