:root {
  font-family: "Prompt";
  font-weight: normal;
  cursor: default;
}
:root .content {
  margin-top: 170px;
}
:root .content p {
  text-align: center;
  letter-spacing: 2px;
}
:root a {
  text-decoration: none;
}
:root ul {
  text-decoration: none;
  padding: 0 0 0 0;
}
:root ul li {
  list-style-type: none;
