body {
  margin: 0;
  display: grid;
  background-color: var(--background-color-primary);
  color: white;
}
body header {
  z-index: 1;
  height: 120px;
  color: white;
}
body header.down-scroll {
  position: fixed;
  top: 0px;
  width: 100%;
  transition: top 0.3s;
  background-color: var(--background-color-primary-opacity);
  backdrop-filter: blur(5px);
}
body header .container-1 {
  height: 100%;
  width: 100%;
  display: flex;
  font-family: "Prompt";
  letter-spacing: 3px;
  justify-content: space-between;
}
body header .container-1 img {
  cursor: pointer;
  height: 5rem;
  height: 5rem;
  margin-top: 1rem;
  margin-left: 4rem;
  transition: transform 0.3s;
}
body header .container-1 img:hover {
  transform: scale(1.05);
}
body header .container-1 .menu-container {
  width: 70%;
  display: flex;
  align-items: center;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
}
body header .container-1 .menu-container p {
  width: fit-content;
  padding: 0 15px;
  margin: 0.5rem 0;
  cursor: pointer;
}
body header .container-1 .menu-container p a {
  margin: 0;
}
body header .container-1 .menu-container p a:hover {
  transition: 0.5s;
  color: var(--blue-primary);
}
body header .container-1 .nous-rejoindre {
  border-top: var(--blue-primary) solid 2px;
  border-left: var(--blue-primary) solid 2px;
  border-bottom: #FF3131 solid 2px;
  border-right: #FF3131 solid 2px;
  border-radius: 5px;
  letter-spacing: normal;
  height: fit-content;
  padding: 0 2rem 0 2rem;
  display: flex;
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 3rem;
}
body header .container-1 .nous-rejoindre, body header .container-1 .nous-rejoindre * {
  cursor: pointer;
}
body header .container-1 .nous-rejoindre span.point {
  background-color: var(--blue-primary);
  border-radius: 50%;
  height: 12px;
  width: 12px;
  margin-right: 10px;
  margin-top: auto;
  margin-bottom: auto;
}
body header .container-1 .nous-rejoindre p {
  min-width: 10rem;
  margin: 0;
}
body header .container-1 .nous-rejoindre:hover {
  transition: 0.2s;
  border: var(--blue-primary) solid 2px;
  color: var(--blue-primary);
}
body header .container-1 .nous-rejoindre:hover span.point {
  transition: 1.2s;
  background-color: white;
}
body header .container-2 nav {
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}
body header .container-2 nav span {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  width: auto;
  background-color: #252629;
  flex: 1;
  align-items: center;
  border-left: 2px solid var(--background-color-primary);
  border-right: 2px solid var(--background-color-primary);
  border-radius: 5px;
  overflow: hidden;
}
body header .container-2 nav span p,
body header .container-2 nav span a {
  padding: 50% 0;
  text-align: center;
  font-size: 0.9rem;
  color: white;
  width: 100%;
}
body header .container-2 nav span:hover {
  background-color: var(--blue-primary);
}
body header .active {
  color: var(--blue-primary);
}
body .content {
  padding: 0 2.5rem 4rem 2.5rem;
}
body .content section {
  margin-top: 2rem;
  margin-bottom: 6rem;
}
body .content section .span-image {
  border-radius: 5px;
}
body footer {
  padding: 1rem 2rem;
}
body footer .footer-container {
  padding: 2rem 0;
  display: flex;
  flex-wrap: wrap;
}
body footer .footer-container div {
  margin-right: 6rem;
}
body footer .footer-container h3 {
  font-size: 1.8rem;
  max-width: 50px;
}
body footer .footer-container .footer-register {
  flex: 0.4;
}
body footer .footer-container .footer-register input {
  width: 99%;
  background-color: var(--background-color-general);
  border: none;
  border-bottom: var(--input-border-color) solid 1px;
  font-size: 20px;
  margin: 3rem 0 0 0;
  color: var(--text-color-primary);
}
body footer .footer-container .footer-register input:focus {
  outline: none;
}
body footer .footer-container .footer-register .footer-submit {
  background-color: var(--blue-primary);
  padding: 0.75rem 20rem;
  border-radius: 15px;
  margin: 3rem 0 0 0;
}
body footer .footer-container .footer-coordonnees {
  flex: 0.3;
}
body footer .footer-container .footer-coordonnees li {
  margin-bottom: 2rem;
}
body footer .footer-container .footer-coordonnees li span p {
  margin: 0;
}
body footer .footer-container .footer-services {
  flex: 0.3;
}
body footer .footer-container .footer-services li {
  margin-bottom: 0;
  padding-bottom: 1rem;
}
body footer .footer-separator {
  height: 2px;
  background-color: var(--text-color-primary);
}
body footer .footer-container2 {
  display: flex;
  padding-top: 2rem;
}
body footer .footer-container2 .footer-links {
  flex: 0.4;
  display: flex;
  /* align-items: center; */
  flex-wrap: wrap;
}
body footer .footer-container2 .footer-links p {
  margin: 0;
  padding: 0 2rem;
}
body footer .footer-container2 .footer-icons {
  flex: 0.2;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
body footer .footer-container2 .footer-icons img {
  height: 2rem;
  width: 2rem;
}
body footer .footer-container2 .footer-mention {
  flex: 0.4;
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;
